@import url("https://fonts.googleapis.com/css?family=Roboto:400,700");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap");
:root {
  --black: #272727;
  --vibrantBlue: #44e0dc;
  --vibrantYellow: #ffed65;
  --vibrantSalmon: #f07c79;
}

html {
  box-sizing: border-box;
}

html:focus-within {
  scroll-behavior: smooth;
}

*,
:after,
:before {
  box-sizing: inherit;
}

* {
  padding: 0;
  margin: 0;
}

body {
  width: 100%;
  height: 100%;
  font-family: sans-serif;
  overflow-x: visible;
}

a {
  color: inherit;
  text-decoration: none;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

address {
  display: inline;
  font-style: normal;
}

iframe {
  border: 0;
}

:root {
  --w: 300px;
  --px: 15px;
  --py: 30px;
}
@media only screen and (min-width: 768px) {
  :root {
    --w: 600px;
  }
}
@media only screen and (min-width: 1024px) {
  :root {
    --w: 900px;
  }
}
@media only screen and (min-width: 1440px) {
  :root {
    --w: 1084px;
  }
}

body {
  display: flex;
  justify-content: center;
}

.wrapper {
  width: var(--w);
  display: flex;
}

section {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-block: var(--py);
}

body {
  font-family: "Roboto", sans-serif;
  line-height: 1.5rem;
  letter-spacing: 1.5;
}

.title, .timeline__content__title, .timeline__title {
  font-size: 2.125rem;
  font-weight: 700;
}

.robotoMono, .timeline__content__title, .timeline__title::before {
  font-family: "Roboto Mono", monospace;
}

.bigNumber, .timeline__title::before {
  font-size: 5rem;
}

:root {
  --timelineBorder: 10px solid white;
  --timelineBorderRadius: 50px;
}

.timeline {
  background-image: linear-gradient(180deg, #272727, #464646);
  color: white;
  padding-top: 6.25rem;
}

.timeline__list {
  counter-reset: timeline-counter;
  width: 35rem;
  margin-left: auto;
  margin-right: auto;
}

.timeline__item {
  display: flex;
  position: relative;
  border: var(--timelineBorder);
  border-color: transparent;
  border-radius: var(--timelineBorderRadius);
}
.timeline__item:nth-of-type(3n + 1) {
  color: var(--vibrantBlue);
}
.timeline__item:nth-of-type(3n + 2) {
  color: var(--vibrantYellow);
}
.timeline__item:nth-of-type(3n + 3) {
  color: var(--vibrantSalmon);
}
.timeline__item:nth-of-type(odd) {
  border-top: var(--timelineBorder);
  border-right: var(--timelineBorder);
}
.timeline__item:nth-of-type(even) {
  border-top: var(--timelineBorder);
  border-left: var(--timelineBorder);
}
.timeline__item:last-child {
  border-color: transparent;
}
.timeline__item:last-child::before {
  content: "";
  position: absolute;
  background-color: white;
  height: 0.625rem;
  width: 50%;
  right: 0;
  top: -0.625rem;
}
.timeline__item:last-child::after {
  content: "";
  position: absolute;
  background-color: #d8d8d8;
  width: 2.25rem;
  height: 2.25rem;
  border-radius: 50%;
  top: -1.4375rem;
  left: 50%;
  transform: translate(-50%);
}

.timeline__header {
  position: absolute;
  width: 15.625rem;
}
.timeline__item:nth-of-type(odd) .timeline__header {
  left: -20.625rem;
}
.timeline__item:nth-of-type(even) .timeline__header {
  right: -20.625rem;
}

.timeline__icon {
  display: flex;
  width: 5.75rem;
  height: 5.75rem;
  border-radius: 50%;
  background-color: currentColor;
  padding: 1.25rem;
  position: absolute;
  isolation: isolate;
}
.timeline__icon:focus {
  outline: currentColor 0.625rem solid;
  outline-offset: 0.3125rem;
}
.timeline__item:nth-of-type(odd) .timeline__icon {
  top: -3.125rem;
  right: -7.5rem;
}
.timeline__item:nth-of-type(even) .timeline__icon {
  top: -3.75rem;
  left: -7.5rem;
}
.timeline__icon::before {
  content: "";
  position: absolute;
  width: 8.75rem;
  height: 8.75rem;
  background-color: currentColor;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  border-radius: inherit;
  opacity: 0;
  z-index: -1;
  transition: opacity 0.3s ease-in-out, transform 0.2s ease-in-out;
}
.timeline__icon:hover::before {
  opacity: 0.5;
  transform: translate(-50%, -50%) scale(1);
}
.open .timeline__icon::before {
  transform: translate(-50%, -50%) scale(1);
  opacity: 0.5;
}

.timeline__title {
  position: relative;
  display: block;
  margin-top: 2.5rem;
}
.timeline__item:nth-of-type(odd) .timeline__title {
  text-align: right;
}
.timeline__title::before {
  counter-increment: timeline-counter;
  content: counter(timeline-counter) ".";
  position: absolute;
  top: -4.375rem;
}
.timeline__item:nth-of-type(odd) .timeline__title::before {
  right: 0;
}
.timeline__item:nth-of-type(even) .timeline__title::before {
  left: 0;
}

.timeline__content {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  padding-block: 1.875rem;
  transform: scale(0);
  max-height: 6.25rem;
  opacity: 0;
  transition: transform 0.6s ease-in-out, max-height 0.6s ease-in-out, opacity 0.8s ease-in-out;
}
.open .timeline__content {
  transform: scale(1);
  max-height: 100vh;
  opacity: 1;
}

.timeline__content__title {
  margin-top: 4rem;
  margin-bottom: 2rem;
}

.timeline__content__paragraph {
  color: white;
  margin-top: 1rem;
}
.timeline__content__paragraph:last-child {
  margin-bottom: 3rem;
}